.row {
  width: 100%; }

.container {
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto; }

.center {
  text-align: center; }

@-webkit-keyframes blink {
  0% {
    opacity: 0; }
  33%,
  66% {
    opacity: 1; }
  to {
    opacity: .6; } }

@keyframes blink {
  0% {
    opacity: 0; }
  33%,
  66% {
    opacity: 1; }
  to {
    opacity: .6; } }

@-webkit-keyframes floatUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes floatUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.HomePage {
  width: 100%; }


